import loginAPI from "./loginAPI";

export default {
  async login(uid, password) {
    try {
      const loginResponse = await loginAPI.login(uid, password);
      alert('id : ' + loginResponse.uid);
      alert('pwd : ' + loginResponse.passowrd);
      return loginResponse;
    } catch (err) {
      console.error(err);
    }
  }
};
